.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
 #latest{
  position: relative;
  height: 600;
  left: 0;
}

.containbox{
  /* background-color: rgba(222, 213, 213, 0.843); */
  position: fixed;
  left: 0;
  height: 60%;
  width: 300px;
}
#newscontainer{
  margin-left: 120px;
  margin-right: 200px;
}
li{
  list-style: none;
}
#latestlink:hover{
  color: red;
}
#latestlink::after{
  color: red;
}
